
.title-btn {
    width: 100%;
    height: 30px;
}
.slider-enter {
    height: 0px !important;
}
.slider-enter-active {
    transition: all 0.7s linear;
    overflow: hidden;
}
.slider-leave-active {
    /*transform-origin: 50% 0;
            transform: scale(1,0);*/
    height: 0 !important;
    transition: all 0.7s linear;
    overflow: hidden;
}



