
.slider-box {
    border: solid 1px black;
    position: relative;
    height: 40px;
    width: 400px;
}
.slider-line {
    position: absolute;
    width: 360px;
    height: 6px;
    background-color: orangered;
    left: 20px;
    top: 17px;
}
.slider-point {
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: radial-gradient(white, black);
    /*left: 100px;*/
    top: 10px;
}



